/**
Tab navigation
**/
define('structure/tab/tabNavigation/controller',['require','text!structure/tab/tabNavigation/view.html'],function (require) {
    var tpl = require("text!structure/tab/tabNavigation/view.html");

    var definition = {
        template: tpl,
        build: function (vm, node, callback) {
            var built = _.template(tpl);
            callback(built());
        }
    }

    return definition;

});

